  mounted() {
    // console.log('rentRecommentAction: mounted:' + this.geoStationId)
    this.requestRentRecomment(this.geoHash, 1, this.dtype)
  },
  methods: {
    getRentRecommentParms(page, dtype) {
      return {
        city: utils.getCity(),
        geoHash: this.geoHash
      }
    },
    sectionIndexChanged(index) {
      this.sectionIndex = index
      this.geoStationIndex = 0
      const region = this.regions[index]
      if (this.onRecommendStationChanged) {
        this.onRecommendStationChanged(this.dtype, region.geoStationId)
      }
    },
