    requestRentRecomment(geohash, page, dtype) {
      const params = this.getRentRecommentParms(page, dtype)
      this.$axios
        .get(this.rentRecommentAction, { params })
        .then((res) => {
          if (res.length > 0) {
            const array = []
            res.forEach((element) => {
              if (
                element.geoStationId !== this.geoStationId &&
                element.geoHash !== ''
              ) {
                array.push(element)
              }
            })

            this.regions = utils.unique(array)
            // console.log('recommend array:' + JSON.stringify(array))
            this.showRecommendDiv = array.length > 0
          } else {
            this.showRecommendDiv = false
          }

          // console.log('recommend:' + JSON.stringify(res))
        })
        .catch((e) => {
          console.log('recommend error:' + JSON.stringify(e))
        })
    },
